<template>
	<view style="padding:50rpx;">
		<view style="height:100rpx;"></view>
		<view class="gui-flex gui-rows ">
			<text class="gui-h3">欢迎注册奕文艺舞认证考试中心</text>
			<!-- <text class="login-logo gui-icons gui-color-white gui-block-text gui-border-box gui-bg-orange gui-box-shadow gui-box">&#xe620;</text> -->
		</view>
		<view style="margin-top:80rpx;">
			<form @submit="submit">
				<view class="gui-border-b gui-rows gui-flex gui-space-around">
					<text class="gui-tb-margin gui-color-gray">+86</text>
					<input type="text" class="gui-form-input gui-margin" 
					name="username" placeholder="请输入手机号" v-model="username" @focus="showNameDelet" @blur="showNameDelet" />
					<view class="gui-tb-margin"  @tap="clearNameClick" >
						<text class=" gui-icons" v-if="username.length>0":hidden="isNamehidden">&#xe610;</text>
					</view>
				</view>
				
			
				<view class="gui-color-black gui-h6 gui-tb-margin">
					<checkbox-group @change="checkboxChange" class="gui-inline-block">
						<checkbox :value='checkVal' :checked="checked"/>
					</checkbox-group>
					我已阅读并同意
					<navigator class="gui-inline-block gui-color-gray" target="" url="#" hover-class="navigator-hover" open-type="navigate">
						《用户协议》
					</navigator>、
					<navigator class="gui-inline-block gui-color-gray" target="" url="#" hover-class="navigator-hover" open-type="navigate">
						《隐私政策》
					</navigator>
					<!-- ,并授权我们使用该账号信息(如昵称、头像)进行统一管理。 -->
				</view>
				<view style="margin-top:38rpx;">
					<button type="default" class="gui-button gui-bg-orange1" formType="submit" :disabled="moveToNext"
					style="border-radius:20rpx;">
						<text class="gui-color-white gui-button-text">注册</text>
					</button>
				</view>
			</form>
		
			
		</view>
	
	</view>
</template>
<script>
var graceChecker = require("@/GraceUI5/js/checker.js");
	
export default {
	data() {
		return {
			// checkboox的check属性
			checkVal:'0',
			checked:true,
			moveToNext:false,//按钮禁用（默认不禁用，未勾选同意用户协议时禁用）
			src: '../../static/off.png',
			isNamehidden:'1',
			username: '',
			wechatReg:{}//存储传递的参数
		}
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
	    // console.log("option??",option); //打印出上个页面传递的参数。
		this.wechatReg=option
	 },
	methods: {
	// 清空按钮
	clearNameClick() {
	  this.username= ''
	},  

	// 显示x
	showNameDelet() {
	  this.isNamehidden= !this.isNamehidden
	},
	//check按钮
	checkboxChange(e){
					
					if(e.detail.value=='0'){
					this.checked = true
					this.moveToNext=false
					}else{
					this.checked = false
					this.moveToNext=true
					uni.showToast({title:"请勾选同意", icon:"none"});
					}
				},
	
    //提交
		submit : function(e){
			// 表单数据
			var formData = e.detail.value;
			// console.log(formData);
			// 利用 graceUI 的表单验证工具进行验证
			//定义表单规则
			var rule = [
				{name:"username", checkType : "string", checkRule:"10,11", errorMsg:"请输入正确的手机号"},
				// {name:"password", checkType : "string", checkRule:"6,100", errorMsg:"登录密码至少6个字符"},
			];
			var checkRes = graceChecker.check(formData, rule);
			if(checkRes){ 
				uni.showToast({title:"验证通过!", icon:"none"});
			
				// 传参
				uni.navigateTo({
				    url: '../newregcode/index?id='+this.username+'&openid='+this.wechatReg.openid+'&unionid='+this.wechatReg.unionid
					 +'&gender='+this.wechatReg.gender+'&nickName='+this.wechatReg.nickName
					 +'&avatar='+this.wechatReg.avatar+'&address='+this.wechatReg.city
				});
				// graceJS.navigate('../reg/index?a=1&b=2');
			}else{
				uni.showToast({ title:graceChecker.error, icon: "none" });
			}
		}
	}
}
</script>
<style>
/* .login-logo{width:180rpx; height:180rpx; font-size:80rpx; text-align:center; line-height:120rpx; padding:30rpx; border-radius:18rpx;} */
.other-login-icons{width:88rpx; height:88rpx; text-align:center; font-size:70rpx; margin:20rpx;}
</style>